export function getVocab(groupKey) {
  switch (groupKey) {
    case Group.PRESENTE_AR.key:
      return findVocabPresenteByEnd('ar')
    case Group.PRESENTE_ER.key:
      return findVocabPresenteByEnd('er')
    case Group.PRESENTE_IR.key:
      return findVocabPresenteByEnd('ir').concat(findVocabPresenteByEnd('Ã­r'))
    case Group.PRESENTE_IRR_FIRST.key:
      return findVocabPresenteByGroup('first')
    case Group.PRESENTE_IRR_O_UE.key:
      return findVocabPresenteByGroup('o-ue')
    case Group.PRESENTE_IRR_E_IE.key:
      return findVocabPresenteByGroup('e-ie')
    case Group.PRESENTE_IRR_E_I.key:
      return findVocabPresenteByGroup('e-i')
    case Group.PRESENTE_IRR.key:
      return findVocabPresenteByGroup('irr')

    case Group.PERFECTO.key:
      return findVocabPerfectoByGroup(null)

    case Group.INDEFINIDO_AR.key:
      return findVocabIndefinidoByEnd('ar')
    case Group.INDEFINIDO_ER_IR.key:
      return findVocabIndefinidoByEnd('er')
        .concat(findVocabIndefinidoByEnd('ir'))
        .concat(findVocabIndefinidoByEnd('Ã­r'))
    case Group.INDEFINIDO_IRR_ROOT.key:
      return findVocabIndefinidoByGroup("root")
    case Group.INDEFINIDO_IRR_UIR.key:
      return findVocabIndefinidoByGroup("uir")
    case Group.INDEFINIDO_IRR_E_I.key:
      return findVocabIndefinidoByGroup("e-i")
    case Group.INDEFINIDO_IRR.key:
      return findVocabIndefinidoByGroup("irr")

    case Group.IMPERFECTO_AR.key:
      return findVocabImperfecto('ar')
    case Group.IMPERFECTO_ER_IR.key:
      return findVocabImperfecto('er')
        .concat(findVocabImperfecto('ir'))
        .concat(findVocabImperfecto('Ã­r'))
    case Group.IMPERFECTO_IRR.key:
      return findVocabImperfectoByGroup("irr")

    case Group.FUTURO_IMP.key:
      return findVocabFuturoImpByGroup(null)
    case Group.FUTURO_IMP_IRR.key:
      return findVocabFuturoImpByGroup('irr')
    default:
      return null;
  }
}

function findVocabPresenteByGroup(group) {
  return Object.entries(dictionary).filter(([word, desc]) => {
    return !word.endsWith('se') && desc.comoGustar == null && (desc.presente?.group || null) === group
  }).map(el => el[0]);
}

function findVocabPresenteByEnd(end) {
  return Object.entries(dictionary).filter(([word, desc]) => {
    return word.endsWith(end) && desc.comoGustar == null && desc.presente?.group == null
  }).map(el => el[0]);
}

function findVocabPerfectoByGroup(group) {
  return Object.entries(dictionary).filter(([word, desc]) => {
    return !word.endsWith('se') && desc.comoGustar == null && (desc.perfecto?.group || null) === group
  }).map(el => el[0]);
}

function findVocabIndefinidoByEnd(end) {
  return Object.entries(dictionary).filter(([word, desc]) => {
    return word.endsWith(end) && desc.comoGustar == null && desc.indefinido?.group == null
  }).map(el => el[0]);
}

