export function conjugar(tiempo, verbo, persona) {
  if (tiempo === Tiempo.PRESENTE) {
    return presente(verbo, persona)
  } else if (tiempo === Tiempo.INDEFINIDO) {
    return indefinido(verbo, persona)
  } else if (tiempo === Tiempo.IMPERFECTO) {
    return imperfecto(verbo, persona)
  } else if (tiempo === Tiempo.PERFECTO) {
    return perfecto(verbo, persona)
  } else if (tiempo === Tiempo.FUTURO_IMP) {
    return futuroImp(verbo, persona)
  }
}

export function presente(verbo, persona) {
  const dict = dictionary[verbo].presente
  const special = dict?.[persona + 1]
  if (special) return special
  let base = verbo.slice(0, -2)
  if (dict?.root && persona !== 3 && persona !== 4) {
    base = dict.root
  }
  if (verbo.endsWith('ar')) {
    const ends = ['o', 'as', 'a', 'amos', 'Ã¡is', 'an']
    return base + ends[persona]
  } else if (verbo.endsWith('er')) {
    const ends = ['o', 'es', 'e', 'emos', 'Ã©is', 'en']
    return base + ends[persona]
  } else {
    const ends = ['o', 'es', 'e', 'imos', 'Ã­s', 'en']
    return base + ends[persona]
  }
}

export function indefinido(verbo, persona) {
  const dict = dictionary[verbo].indefinido
  const special = dict?.[persona + 1]
  if (special) return special
  if (dict?.root) {
    const ends = ['e', 'iste', 'o', 'imos', 'isteis', 'ieron']
    return dict?.root + ends[persona]
  }
  const base = verbo.slice(0, -2)
  if (verbo.endsWith('ar')) {
    const ends = ['Ã©', 'aste', 'Ã³', 'amos', 'asteis', 'aron']
    return base + ends[persona]
  } else {
    const ends = ['Ã­', 'iste', 'iÃ³', 'imos', 'isteis', 'ieron']
    return base + ends[persona]
  }
}

export function futuroImp(verbo, persona) {
  const root = dictionary[verbo].futuroImp?.root || verbo
  const ends = ['Ã©', 'Ã¡s', 'Ã¡', 'emos', 'Ã©is', 'Ã¡n']
  return root + ends[persona]
}

export function imperfecto(verbo, persona) {
  const dict = dictionary[verbo].imperfecto
  const special = dict?.[persona + 1]
  if (special) return special

  const base = verbo.slice(0, -2)
  if (verbo.endsWith('ar')) {
    const ends = ['aba', 'abas', 'aba', 'Ã¡bamos', 'abais', 'aban']
    return base + ends[persona]
  } else {
    const ends = ['Ã­a', 'Ã­as', 'Ã­a', 'Ã­amos', 'Ã­ais', 'Ã­an']
    return base + ends[persona]
  }

}

export function perfecto(verbo, persona) {
  const haber = ['he', 'has', 'ha', 'hemos', 'habÃ©is', 'han']
  return haber[persona] + ' ' + participio(verbo)

}

function participio(verbo) {
  const base = verbo.slice(0, -2)
  if (verbo.endsWith('ar')) {
    return base + 'ado'
  } else {
    return base + 'ido'
  }
}
