import * as React from "react";
import {useState} from "react";
import IconButton from "@mui/material/IconButton";
import {Settings} from "@mui/icons-material";
import Container from "@mui/material/Container";
import FormControl from "@mui/material/FormControl";
import FormLabel from "@mui/material/FormLabel";
import FormGroup from "@mui/material/FormGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import Switch from "@mui/material/Switch";
import {Group} from "../lang/grammatica";
import SwipeableDrawer from "@mui/material/SwipeableDrawer";

export function GroupSettings({initGroups, onClose}) {

  const [isSettingsOpen, setSettingsOpen] = useState(false)
  const [enabledGroups, setEnabledGroups] = useState(initGroups)

  function switchEnabledGroup(group) {
    setEnabledGroups(currGroups => {
      if (currGroups.includes(group)) {
        if (currGroups.length > 1) {
          return currGroups.filter(grp => grp !== group)
        } else {
          return currGroups
        }
      } else {
        return currGroups.concat(group)
      }
    })
  }

  return (
    <IconButton>
      <Settings fontSize="large" color="secondary" onClick={() => setSettingsOpen(true)}/>
      <SwipeableDrawer
        anchor="right"
        open={isSettingsOpen}
        onClose={() => {
          setSettingsOpen(false)
          onClose(enabledGroups)
        }}
      >
        <Container component="main" maxWidth="xs">
          <FormControl component="fieldset" variant="standard">
            <FormLabel component="legend">Presente</FormLabel>
            <FormGroup>
              <FormControlLabel
                control={
                  <Switch checked={enabledGroups.includes(Group.PRESENTE_AR.key)}
                          onChange={() => switchEnabledGroup(Group.PRESENTE_AR.key)}
                  />
                }
                label={Group.PRESENTE_AR.name}
              />
              <FormControlLabel
                control={
                  <Switch checked={enabledGroups.includes(Group.PRESENTE_ER.key)}
                          onChange={() => switchEnabledGroup(Group.PRESENTE_ER.key)}
                  />
                }
                label={Group.PRESENTE_ER.name}
              />
              <FormControlLabel
                control={
                  <Switch checked={enabledGroups.includes(Group.PRESENTE_IR.key)}
                          onChange={() => switchEnabledGroup(Group.PRESENTE_IR.key)}
                  />
                }
                label={Group.PRESENTE_IR.name}
              />
              <FormControlLabel
                control={
                  <Switch checked={enabledGroups.includes(Group.PRESENTE_IRR_E_IE.key)}
                          onChange={() => switchEnabledGroup(Group.PRESENTE_IRR_E_IE.key)}
                  />
                }
                label={Group.PRESENTE_IRR_E_IE.name}
              />
              <FormControlLabel
                control={
                  <Switch checked={enabledGroups.includes(Group.PRESENTE_IRR_O_UE.key)}
                          onChange={() => switchEnabledGroup(Group.PRESENTE_IRR_O_UE.key)}
                  />
                }
                label={Group.PRESENTE_IRR_O_UE.name}
              />
              <FormControlLabel
                control={
                  <Switch checked={enabledGroups.includes(Group.PRESENTE_IRR_E_I.key)}
                          onChange={() => switchEnabledGroup(Group.PRESENTE_IRR_E_I.key)}
                  />
                }
                label={Group.PRESENTE_IRR_E_I.name}
              />
              <FormControlLabel
                control={
                  <Switch checked={enabledGroups.includes(Group.PRESENTE_IRR_FIRST.key)}
                          onChange={() => switchEnabledGroup(Group.PRESENTE_IRR_FIRST.key)}
                  />
                }
                label={Group.PRESENTE_IRR_FIRST.name}
              />
              <FormControlLabel
                control={
                  <Switch checked={enabledGroups.includes(Group.PRESENTE_IRR.key)}
                          onChange={() => switchEnabledGroup(Group.PRESENTE_IRR.key)}
                  />
                }
                label={Group.PRESENTE_IRR.name}
              />
            </FormGroup>
            <FormLabel component="legend">PretÃ©rito Perfecto</FormLabel>
            <FormGroup>
              <FormControlLabel
                control={
                  <Switch checked={enabledGroups.includes(Group.PERFECTO.key)}
                          onChange={() => switchEnabledGroup(Group.PERFECTO.key)}
                  />
                }
                label={Group.PERFECTO.name}
              />
            </FormGroup>
            <FormLabel component="legend">PretÃ©rito Indefinido</FormLabel>
            <FormGroup>
              <FormControlLabel
                control={
                  <Switch checked={enabledGroups.includes(Group.INDEFINIDO_AR.key)}
                          onChange={() => switchEnabledGroup(Group.INDEFINIDO_AR.key)}
                  />
                }
                label={Group.INDEFINIDO_AR.name}
              />
              <FormControlLabel
                control={
                  <Switch checked={enabledGroups.includes(Group.INDEFINIDO_ER_IR.key)}
                          onChange={() => switchEnabledGroup(Group.INDEFINIDO_ER_IR.key)}
                  />
                }
                label={Group.INDEFINIDO_ER_IR.name}
              />
              <FormControlLabel
                control={
                  <Switch checked={enabledGroups.includes(Group.INDEFINIDO_IRR_ROOT.key)}
                          onChange={() => switchEnabledGroup(Group.INDEFINIDO_IRR_ROOT.key)}
                  />
                }
                label={Group.INDEFINIDO_IRR_ROOT.name}
              />
              <FormControlLabel
                control={
                  <Switch checked={enabledGroups.includes(Group.INDEFINIDO_IRR_E_I.key)}
                          onChange={() => switchEnabledGroup(Group.INDEFINIDO_IRR_E_I.key)}
                  />
                }
                label={Group.INDEFINIDO_IRR_E_I.name}
              />
              <FormControlLabel
                control={
                  <Switch checked={enabledGroups.includes(Group.INDEFINIDO_IRR_UIR.key)}
                          onChange={() => switchEnabledGroup(Group.INDEFINIDO_IRR_UIR.key)}
                  />
                }
                label={Group.INDEFINIDO_IRR_UIR.name}
              />
              <FormControlLabel
                control={
                  <Switch checked={enabledGroups.includes(Group.INDEFINIDO_IRR.key)}
                          onChange={() => switchEnabledGroup(Group.INDEFINIDO_IRR.key)}
                  />
                }
                label={Group.INDEFINIDO_IRR.name}
              />
            </FormGroup>
            <FormLabel component="legend">PretÃ©rito Imperfecto</FormLabel>
            <FormGroup>
              <FormControlLabel
                control={
                  <Switch checked={enabledGroups.includes(Group.IMPERFECTO_AR.key)}
                          onChange={() => switchEnabledGroup(Group.IMPERFECTO_AR.key)}
                  />
                }
                label={Group.IMPERFECTO_AR.name}
              />
              <FormControlLabel
                control={
                  <Switch checked={enabledGroups.includes(Group.IMPERFECTO_ER_IR.key)}
                          onChange={() => switchEnabledGroup(Group.IMPERFECTO_ER_IR.key)}
                  />
                }
                label={Group.IMPERFECTO_ER_IR.name}
              />
              <FormControlLabel
                control={
                  <Switch checked={enabledGroups.includes(Group.IMPERFECTO_IRR.key)}
                          onChange={() => switchEnabledGroup(Group.IMPERFECTO_IRR.key)}
                  />
                }
                label={Group.IMPERFECTO_IRR.name}
              />
            </FormGroup>
            <FormLabel component="legend">Futuro Imperfecto</FormLabel>
            <FormGroup>
              <FormControlLabel
                control={
                  <Switch checked={enabledGroups.includes(Group.FUTURO_IMP.key)}
                          onChange={() => switchEnabledGroup(Group.FUTURO_IMP.key)}
                  />
                }
                label={Group.FUTURO_IMP.name}
              />
              <FormControlLabel
                control={
                  <Switch checked={enabledGroups.includes(Group.FUTURO_IMP_IRR.key)}
                          onChange={() => switchEnabledGroup(Group.FUTURO_IMP_IRR.key)}
                  />
                }
                label={Group.FUTURO_IMP_IRR.name}
              />
            </FormGroup>
          </FormControl>
        </Container>
      </SwipeableDrawer>
    </IconButton>

  );
}